@import '_variables';
@import '_syntax';

* {
  margin: 0;
  padding: 0;
}

::-moz-selection {
  background-color: $text-color;
  color: $page-color;
}
::selection {
  background-color: $text-color;
  color: $page-color;
}

html {
  background-color: $accent-color;
}

body {
  text-align: center;
  color: mix($text-color, $page-color, 90%);
  font-family: 'ITC Avant Garde Gothic Std Book', sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.8;
  min-height: 100vh;
  @media (min-width: $narrow) {
    font-size: 18px;
  };

  &:not(.home) {
    background-color: $page-color;
    background: $page-color;
    background: url(/images/bg-grid.png), linear-gradient(320deg, $page-color 0%, $accent-color 100%);
  }
}

img.logo {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.container {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  text-align: left;
  @media (min-width: $mid) {
    width: 650px;
  };
  @media (min-width: $wide) {
    width: 750px;
  };
}


h1, h2, h3 {
  color: $text-color;
  font-weight: 400;
  margin-top: 2em;
  margin-bottom:0.2em;
  line-height: 1;
}
h1 {
  font-size: 2em;
  margin-top: 0.4em;
  margin-bottom: 0.2em;
  line-height: 1;

  @media (min-width: $mid) {
    font-size: 3em;
  };
  @media (min-width: $wide) {
    font-size: 4em;
  };
}

p {
  margin-top: 1em;
  margin-bottom: 1em;
}

.subtitle {
  font-size: 1.2em;
  margin-top: 1em;
  margin-bottom: 2.5em;
}

ul, ol {
  padding-left: 1em;
}

a:link,
a:visited {
  color: $text-color;
  text-decoration: none;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: mix($text-color, $accent-color, 20%)
}
a:hover,
a:focus {
  color: $text-color;
  border-bottom-color: $text-color;
}

.nakedLink a:link,
.nakedLink a:visited,
.nakedLink a:hover,
.nakedLink a:focus {
  border-bottom-style:none;
}

nav ul {
  padding-top: 3em;

  li {
    display: inline;
  }

  a:link,
  a:visited {
    display: inline-block;
    border-top-style: solid;
    border-top-width: 1px;
    border-top-color: mix($text-color, $accent-color, 40%);
    border-bottom-style: none;
    padding-top: 0.8em;
    padding-bottom: 2em;
    margin-left: 0;
    margin-right: 0.7em;
    width: 7em;
    text-align: left;
    color: mix($text-color, $accent-color, 80%);
    text-decoration: none;
  }
  a:hover,
  a:focus {
      color: $text-color;
      border-top-color: $text-color;
      border-bottom-style: none;
  }

  small {
    display: block;
    font-size: 0.7em;
    color: mix($text-color,$accent-color, 60%);
  }
}


code {
  font-size: 0.8em;
  background-color: mix($text-color,$accent-color, 80%);
  color: rgb(42, 65, 77);
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 6px;
  padding-right: 6px;
  border-radius: 4px;
  white-space: nowrap;
}

.date {
  font-size: 0.8em;
  font-style: italic;
}

main {
  margin-top: 4em;
}

pre {
  overflow: auto;
  border-radius: 6px;
  code {
    border-style: none;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
  }
}


footer {
  display: flex;
  margin-top: 6em;
  padding-top: 4em;
  padding-bottom: 6em;
  border-top-width: 1px;
  border-top-style: solid;
  border-top-color: mix($text-color, $accent-color, 10%);
  font-size: 0.7em;
  color: mix($text-color, $accent-color, 50%);

  div.col {
    float: left;

    p:nth-child(1) {
      margin-top: 0;
    }
  }

  a:link,
  a:visited {
    color: mix($text-color, $accent-color, 90%);
    border-bottom: solid 1px mix($accent-color, $page-color, 70%);
  }
  a:hover,
  a:focus {
    color: $text-color;
    border-bottom: solid 1px $text-color;
  }

  div.logo {
    width: 64px;
    height: 64px;
    margin-right: 1.5em;
    background-color: mix($text-color, $accent-color, 50%);
    -webkit-mask: url(/images/logo_square_alpha.svg) no-repeat center;
    mask-size: contain;
    -webkit-mask-size: contain;
    mask: url(/images/logo_square_alpha.svg) no-repeat center;

    &:hover {
      background-color: mix($text-color, $accent-color, 80%);
    }
  }
}

div.background {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: $page-color;
  overflow: hidden;
}